.calculator-wrapper {
	border-bottom: 0.5px solid var(--light-grey);
}
.calculator {
	text-align: center;
	padding-top: 6rem;
	padding-bottom: 6rem;
}
.calculator__title {
	left: -2.75em;
}
.calculator__loading {
	font-size: 3rem;
	margin-top: 100px;
}


@media(--sm) {
	.calculator-wrapper {
		border-bottom: none;
	}
	.calculator__title {
		left: -2.25em;
	}
}

@media(--ph) {
	.calculator {
		padding-top: 8rem;
	}
}