.calculator__legal-forms,
.calculator__taxes-types {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-bottom: 2rem;
}

.calculator__legal-forms-item,
.calculator__taxes-types-item {
    display: flex;
    flex-direction: row;
    align-items: center;
    align-content: center;
    cursor: pointer;
}

.calculator__legal-forms-item label,
.calculator__taxes-types-item label {
    padding: 1rem;
}

.calculator__operations {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-bottom: 2rem;
    margin-top: 70px;
}

.calculator__operations-slider {
    width: 80%;
    max-width: 450px;
}

.calculator__operations-count {
    margin-left: 40px;
    font-size: 20px;
    font-weight: bold;
    width: 20%;
    max-width: 240px;
}

.row__item {
    display: inline-block;
    border-radius: 50%;
    width: 1rem;
    height: 1rem;
    background: lightgrey;
}

.row__item_included {
    border-radius: 0;
    width: 2rem;
    height: 1.5rem;
    background-color: transparent;
    background-image: url('img/svg/include.svg');
    background-size: contain;
    background-repeat: no-repeat;
}

.calculator__legal-forms-item:not(:last-of-type) {
    margin-right: 50px;
}
.calculator__legal-forms-item input {
    position: absolute;
    z-index: -1;
    opacity: 0;
    margin: 10px 0 0 7px;
}
.calculator__legal-forms-item input + label {
    position: relative;
    padding: 0 0 0 70px;
    font-size: 30px;
    font-weight: bold;
    cursor: pointer;
}
.calculator__legal-forms-item input + label:before {
    content: '';
    position: absolute;
    top: -5px;
    left: 0;
    width: 40px;
    height: 40px;
    border: 3px solid var(--light-grey);
    border-radius: 50%;
    background: #FFF;
}
.calculator__legal-forms-item input:checked + label:after {
    content: '';
    position: absolute;
    top: 3px;
    left: 8px;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background: var(--yellow);
}

.calculator__operations-slider .vue-slider-component .vue-slider-dot {
    width: 45px !important;
    height: 45px !important;
    top: -20px !important;
    background-color: var(--yellow);
    box-shadow: none;
}
.calculator__taxes-types {
    display: none;
}
.calculator__results {
    margin-top: 80px;
}

.results {
    border-collapse: collapse;
}
.results thead {
    background: var(--yellow);
    font-size: 20px;
    border: 1px solid var(--yellow);
}
.results thead th {
    padding: 20px;
    position: relative;
}
.results thead th:not(:last-child)::before {
    content: "";
    position: absolute;
    top: calc(50%-12px);
    right: 0;
    z-index: 1;
    height: 24px;
    width: 1px;
    background-color: var(--black);
}
.results tbody tr {
    position: relative;
}

.results tbody {
    border: 1px solid var(--light-grey);
    font-size: 1.3rem;
}
.results tbody td {
    padding: 30px;
    position: relative;
    min-width: 120px;
}
.results tbody td:first-child {
    text-align: left;
    width: 30%;
}
.results tbody td:not(:last-child)::before {
    content: "";
    position: absolute;
    top: calc(50%-12px);
    right: 0;
    z-index: 1;
    height: 24px;
    width: 1px;
    background-color: var(--light-grey);
}
.results tbody td::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 1;
    height: 1px;
    width: 100%;
    background-color: var(--light-grey);
}
.results tbody td:first-child::after {
    left: 30px;
    width: calc(100%-30px);
}
.results tbody td:last-child::after {
    right: 30px;
    width: calc(100%-30px);
}
.results tbody td[colspan="5"]::before {
    display: none;
}
.results tbody td[colspan="5"]::after {
    right: 30px;
    width: calc(100%-30px);
}
.results tbody tr:last-child td::after {
    display: none;
}

#results-null tbody tr:not(:first-child) {
    display: none;
}
#results-null tbody tr:first-child td::after {
    display: none;
}

.calculator .vue-slider-component .vue-slider-process {
    background: var(--yellow);
    height: 105%;
}
.calculator .vue-slider-component .vue-slider {
    background: var(--black);
}

@media screen and (max-width: 680px) {
    .column_hidden {
        display: none;
    }
}

.results {
    display: block;
}

.results_hidden {
    display: none;
}

@media(--ph) {
    .calculator__legal-forms {
        flex-wrap: wrap;
        padding: 0 20px;
    }
    .calculator__operations {
        flex-direction: column-reverse;
        align-items: center;
        margin-bottom: 6rem;
    }
    .calculator__operations-count {
        margin-bottom: 30px;
        margin-left: 0;
        font-size: 25px;
        width: 100%;
        max-width: 300px;
    }
    .calculator__taxes-types {
        display: flex;
        justify-content: center;
    }
    .calculator__taxes-types-item {
        margin-bottom: 25px;
    }
    .calculator__taxes-types-item input {
        position: absolute;
        z-index: -1;
        opacity: 0;
        margin: 10px 0 0 7px;
    }
    .calculator__taxes-types-item input + label {
        position: relative;
        padding: 0 0 0 50px;
        font-size: 25px;
        font-weight: bold;
        cursor: pointer;
    }
    .calculator__taxes-types-item input + label:before {
        content: '';
        position: absolute;
        top: 1px;
        left: 0;
        width: 20px;
        height: 20px;
        border: 3px solid var(--yellow);
        border-radius: 50%;
        background: #FFF;
    }
    .calculator__taxes-types-item input:checked + label:before {
        background: var(--yellow);
    }
    .results thead th:not(:first-child)::before {
        display: none;
    }
    .results tbody td:not(:first-child)::before {
        display: none;
    }
    .calculator {
        padding-left: 0;
        padding-right: 0;
    }
    .results tbody td:first-child::after {
        left: 0;
        width: 100%;
    }
    .results tbody td[colspan="5"]::after {
        right: 0;
        width: 100%;
    }
    .results tbody td:first-child {
        width: 50%;
    }
}
@media(max-width: 643px) {
    .calculator__legal-forms-item:last-child {
        margin-top: 40px;
    }
}
@media(max-width: 500px) {
    .calculator table {
        width: 100%;
        overflow: auto;
    }
    .calculator table td {
        padding: 15px;
    }
}
@media(max-width: 402px) {
    .calculator__legal-forms-item input+label {
        font-size: 25px;
    }
    .calculator__legal-forms-item input+label:before {
        top: -9px;
    }
    .calculator__legal-forms-item input:checked+label:after {
        top: -1px;
    }
    .calculator__legal-forms-item:first-of-type {
        margin-right: 30px;
    }
    .calculator__legal-forms-item:not(:first-of-type) {
        margin-right: 0;
    }
}